import React, { useState, useEffect } from "react";
import Box from "@mui/material/Box";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import Typography from "@mui/material/Typography";

const typographyStyle = {
  verticalAligne: "top",
  overflow: "visible",
  fontSize: "14px",
  fontWeight: "400",
  lineHeight: "1.4",
  clolr: "#172b4d",
};

export default function Remainder({ defaultValue, remaindeBefore }) {
  const [remainder, setRemainder] = useState(defaultValue);

  const handleChange = (event) => {
    setRemainder(event.target.value);
  };

  useEffect(() => {
    remaindeBefore(remainder);
  }, [remaindeBefore, remainder]);

  useEffect(() => {
    setRemainder(defaultValue);
  }, [defaultValue]);

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        width: "100%",
        marginBottom: "15px",
      }}
    >
      <InputLabel id="remainder-label"> </InputLabel>
      <Select
        labelId="remainder-label"
        id="remainder-select"
        value={remainder}
        label=" "
        onChange={handleChange}
        sx={{
          marginBottom: "10px",
          height: "38px",
          fontSize: "14px",
        }}
      >
        <MenuItem value={-10}> </MenuItem>
        <MenuItem value={0}>   </MenuItem>
        <MenuItem value={5}> 5 </MenuItem>
        <MenuItem value={10}> 10 </MenuItem>
        <MenuItem value={15}> 15 </MenuItem>
        <MenuItem value={60}> 1 </MenuItem>
        <MenuItem value={120}> 2 </MenuItem>
        <MenuItem value={1440}> 1 </MenuItem>
        <MenuItem value={2880}> 2 </MenuItem>
      </Select>
      <Typography sx={typographyStyle}>
              .
      </Typography>
    </Box>
  );
}
